import React from 'react';
import AppBar from 'material-ui/AppBar';

class UserAppBar extends React.Component {
  render() {return(
  <AppBar
    title={this.props.username}
    avatar={this.props.useravatar}
  />
);
}
}
export default UserAppBar;


